import { Component, OnInit } from '@angular/core';
import {SelectItem} from 'primeng/api';
import { PeticionesService } from '../../services/peticiones.service';
import { FuncionesService } from '../../services/funtions.service';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { RouterModule,ActivatedRoute, Router } from '@angular/router';
@Component({
  selector: 'app-reg-terminal-estado',
  templateUrl: './reg-terminal-estado.component.html',
  styleUrls: ['./reg-terminal-estado.component.css']
})
export class RegTerminalEstadoComponent implements OnInit {
	Inpdisplay: boolean;
	formEstado:FormGroup;
	colores: SelectItem[];
	color_select:object;
	id;
	constructor(private aroute:ActivatedRoute,private fb: FormBuilder,private _router: Router,private _peticiones :PeticionesService, public _funService:FuncionesService) {
		this.getAllColors()
		console.log('colorSelect',this.color_select);
	}

	ngOnInit() {
		this.getAllColors()
		this.aroute.params.subscribe( params => {
      		// console.log('params',params);
          	this.id = params["id"]
      	});
		this.createForm();
		if(this.id != null){
			// this.categoria();
		}
		this.Inpdisplay = true;
	}

	colorSelect(color){
		this.color_select = color;
		console.log('colorSelect',color);
	}

	createForm(a={
	  		nombre:'',
	  		estado:true
	  }){
	  	this.formEstado=this.fb.group(a)
	  	this._funService.actionsOnRoute(this.formEstado.controls);
	}


	onSubmit(){
		let cNameAction = "CrearEstado";
	    let value = JSON.parse(JSON.stringify(this.formEstado.value));
	    value.id_color=this.color_select
	    if (this.id!= null){
	      cNameAction ="UpdateEstado";
	      value.id = this.id;
	    }

	    this._funService._peticiones[cNameAction]({value},this.id).subscribe(
	      res => {
	      	console.log("res",res)
	      	this._funService.Toast("success","info",res.msg)
	      	this.OnHIde();
	      },
	      error => {
	      	this._funService.Toast("error","Error",error)
	      	console.log("err",error)
	      }
	  );

  	}


	OnHIde(){
		 let uri="/config";
	    // this.formEstado.reset();
	    this.Inpdisplay = false;
	    this._router.navigate([uri]);
	    // this.Outdisplay.emit(false);
  	}


	getAllColors(){
  		this._peticiones.GetAllColores().subscribe( (response)=>{
  			this.colores = response;
  			console.log('response',response);
  			this.color_select = response[0]
  			// response.forEach((color,index)=>{
  			// 	let _color = {label:color.nombre,value:color.id,name:color.nombre}
  			// 	console.log('_color',_color);
  			// 	this.colores.push(_color)
  			// })
  		},(err)=>{
  			console.log('response 33',err );
  		});
  	}

}



