import { Component, OnInit } from '@angular/core';
import {SelectItem} from 'primeng/api';
import { PeticionesService } from '../../services/peticiones.service';
import { FuncionesService } from '../../services/funtions.service';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { RouterModule,ActivatedRoute, Router } from '@angular/router';
@Component({
  selector: 'app-list-correo',
  templateUrl: './list-correo.component.html',
  styleUrls: ['./list-correo.component.css']
})
export class ListCorreoComponent implements OnInit {
	correos:any[];
	constructor(private aroute:ActivatedRoute,private fb: FormBuilder,private _router: Router,private _peticiones :PeticionesService, public _funService:FuncionesService) { }

	ngOnInit() {
		this.getAllAcciones()
	}

	getAllAcciones(){
  		this._funService._peticiones.GetAllCorreosSubscritos().subscribe( (response)=>{
			console.log('GetAllCorreosSubscritos',response);
			if(response.info==false){
				this.correos = []
			}else{
				this.correos = response
			}
		},(err)=>{console.log('getAllAcciones 33',err );});
  	}



}



