import { Component, OnInit , Input} from '@angular/core';
import {MenuItem} from 'primeng/api';
import {PeticionesService} from '../../services/peticiones.service';
import { WebsocketService } from '../../services/websocket.service';
import { RouterModule,ActivatedRoute, Router } from '@angular/router';
// import * as ol from './../../js/ol';
import * as proj from 'ol/proj';

@Component({
  selector: 'app-search-terminal',
  templateUrl: './search-terminal.component.html',
  styleUrls: ['./search-terminal.component.css']
})
export class SearchTerminalComponent implements OnInit {
	terminalSeleccionado;
	cantidad: string;
	resultadosDeTerminales: any[];
	detalle:any[];
	filteredTerminales:any[];
	Terminales:any[];
	items: MenuItem[];
	@Input() map;
	constructor(private _router: Router,private _peticiones :PeticionesService,private ws:WebsocketService) { }

	ngOnInit() {
		this.ActionInit();

	}

	ActionInit(){
		this.cantidad = "0";
		this.filteredTerminales = [];
		this.Terminales = [];
		this.detalle =[];//[{id:1,nombre:"Producto",cantidad:2,precio:14,disabled:true},{id:1,nombre:"Producto2",cantidad:2,precio:14,disabled:true},{id:1,nombre:"Producto3",cantidad:2,precio:14,disabled:true}]
		this.allTerminales();
		this.terminalSeleccionado = null;
	}

	  onKeyUp(event) {

    	// if(!isNaN(event.key)){
	    //   let existencia = parseInt((this.terminalSeleccionado["existencia"] / this.terminalSeleccionado["cantidad_equivalente"]).toString())
	    //   if( isNaN(parseInt(this.cantidad))){
	    //     this.cantidad ="0";
	    //   }
	    //   if( parseInt(this.cantidad) <= existencia  ){
	    //     // this.cantidad = (parseInt(event.key)).toString()
	    //   }else{
	    //     this.cantidad = existencia.toString();
	    //   }
	    // }
	  }


	calcularExistencia(producto){
	  return parseInt((producto.existencia / producto.cantidad_equivalente).toString())
	}

	buscarEnMapa(){
		let _Y = 19.220953;
      	let _X = -70.526725;
		var center = [parseFloat(this.terminalSeleccionado.lon),parseFloat(this.terminalSeleccionado.lat)];
		// var center = [_X,_Y];
		// console.log('center',center);
        this.map.getView().setCenter(proj['transform'](center, 'EPSG:4326', 'EPSG:3857'));
		this.map.getView().setZoom(19);
        // this.map.getView().setCenter(ol.proj.fromLonLat([_X,_Y]));
		// console.log('onKeyUp,map',this.map)
	}


	search(event) {
		this.filteredTerminales = [];
		for(let i = 0; i < this.Terminales.length; i++) {
		    let brand = this.Terminales[i];
		    // console.log('brand',brand);
		    brand.id_terminal = brand.id_terminal.toString()
		    // console.log("filterTerminales",event.query)
		    if(brand["id_terminal"].toString().indexOf(event.query.toString()) == 0) {
		        this.filteredTerminales.push(brand);
		    }
		}
	}

	allTerminales(){
	    this._peticiones.GetAllTerminales(0).subscribe(
	        res => {
	          // this.Terminales = [{nombre:"Salami",id:2,precio:200},{nombre:"Platano",id:3,precio:50},{nombre:"Yuca",id:4,precio:25}];;
	          res.forEach((item)=>{
	            // console.log("item",item)
	            // if(item.detalles_compra.length>0){
	              // item.nombre += " #" +parseInt((item.existencia / item.cantidad_equivalente).toString())
	            //   item.nombre_temp = item.nombre +" | Existencia "+ item.detalles_compra[0].cantidad+" | RD$"+ item.detalles_compra[0].precio
	              this.Terminales.push(item)
	            // }
	          })
	          // this.Terminales = res;
	          this.filteredTerminales= res;
	          // console.log(res)
	        },
	        err => {
	          // this._funService.Toast("error","Error",err.error && err.error.msg || err)
	          console.log("err",err)
	        }
	    );
	}

}



