    presentarInterseccionesPorTerminal(terminal){
        // this.intersecciones = "Terminal:"+terminal.terminal.id_terminal +" Intersecta con:"
        terminal.terminal.intersecciones.forEach((element,index)=>{
            // console.log('intersectoa a:',element);
            if(terminal.terminal.intersecciones.length-1 == index){
                this.intersecciones +=" "+element.id_terminal+" "
            }else{
                this.intersecciones +=" "+element.id_terminal+","
            }
        })
    }

    presentarIntersecciones(terminales){
        terminales.forEach((terminal,index)=>{
            
            this.intersecciones += "Terminal:"+terminal['id_terminal'] +":";
            if( terminal.intersecciones.length>0){
                terminal.intersecciones.forEach((interseccion,indx)=>{
                  // console.log(terminal['id_terminal']+':intersecciones',interseccion);
                  if(interseccion!=null){
                    if(terminal.intersecciones.length-1 == indx){
                        this.intersecciones +=" ("+interseccion.id_terminal+") "
                    }else{
                        this.intersecciones +=" ("+interseccion.id_terminal+")"
                    }
                  }
                })
            }
        })
    }


