    graficarTerminal(terminal){
        let pLat = terminal.lat_real
        let pLon = terminal.lon_real
        if(terminal.se_movio < terminal.distancia ){
            pLat = terminal.lat_origin||terminal.lat
            pLon = terminal.lon_origin||terminal.lon
        }
        
			let newFeature = this._functMap.createCircle(terminal, parseFloat(terminal.lon_origin), parseFloat(terminal.lat_origin), parseFloat(pLon), parseFloat(pLat), terminal.id_terminal + "", terminal.distancia, this._functMap.terminalEstado(terminal.es_hoy,terminal.puede_vender),terminal.consorcio,terminal.fecha_ultima_session)
        // console.log('newFeature',newFeature[2].U.text);
        try{
            
            if(!terminal.puede_vender || terminal.se_movio > terminal.distancia){
							let newLineMove = this._functMap.createLine(terminal.id_consorcio,terminal.id_terminal,terminal.lon_real,terminal.lat_real,terminal.lon_origin,terminal.lat_origin)
                if( terminal.se_movio > terminal.distancia){
                    console.log('removeFeatureInMap >>',newLineMove);
                    this.removeFeatureInMap('Line_'+terminal.id_terminal)
                    this.removeFeatureInMap('Icon_'+terminal.id_terminal)
                    
                    this.TERMINALES.push(newLineMove)
                    this.SOURCE.addFeature(newLineMove)
                }
                this.TERMINALES.push(newFeature[2])
                this.SOURCE.addFeature(newFeature[2])
            }else{
                // this.removeFeatureInMap('Line_'+terminal.id_terminal)
                // this.removeFeatureInMap('Icon_'+terminal.id_terminal)

                // this.TERMINALES.push(newFeature[2])
            }
        }catch(error){
            console.warn('ERROR > graficarTerminal', error)
        }
        this.TERMINALES.push(newFeature[0])
        this.TERMINALES.push(newFeature[1])
        this.SOURCE.addFeature(newFeature[0])
        this.SOURCE.addFeature(newFeature[1])
    }

