// Unselect previous selected features
    function unselectPreviousFeatures() {
        var i;
        for(i=0; i< selectedFeatures.length; i++) {
            selectedFeatures[i].setStyle(null);
        }
        selectedFeatures = [];
    }



    function displayTooltip(evt) {
      unselectPreviousFeatures();   
      var pixel = evt.pixel;
      var feature = map.forEachFeatureAtPixel(pixel, function(feature) {
        // feature.setStyle([
        //     selectedStyle, 
        //     selectedTextStyleFunction(feature.get('text'))
        // ]);
        // selectedFeatures.push(feature);
        return feature;
      });
      try{
          if(feature.get('consorcio')!=undefined){
            tooltip.style.display = feature ? '' : 'none';
            if (feature) {
                let puede_vender = 'NO'
                if(feature.get('puede_vender')==1){ puede_vender = 'SI' ;}
                overlay.setPosition(evt.coordinate);
                tooltip.innerHTML = "Consorcio: "+feature.get('consorcio')
                +"<br>Terminal         : "+feature.get('text')
                +"<br>Prioridad        : "+feature.get('prioridad')
                +"<br>Radio            : "+feature.get('radio')+" Metros"
                +"<br>Se Movio         : "+feature.get('se_movio')+" Metros / "+(feature.get('se_movio')/1000).toFixed(2)+" Km"
                +"<br>Ultima Sessin   : "+feature.get('ultima_session')
                +"<br>Fecha de Creacin: "+feature.get('fecha_creacion')
                +"<br>Puede Vender     : "+puede_vender;
            }
          }else{
              tooltip.style.display ='none';
          }
      }catch(e){
           tooltip.style.display ='none';
      }
    };

    map.on('pointermove', displayTooltip);
    map.getInteractions().extend([selectInteraction]);
    map.addInteraction(selectClick);
    map.addControl(scaleLineControl);
    map.addControl(mousePosition);

