import { Component, OnInit } from '@angular/core';
import { PeticionesService } from '../../services/peticiones.service';
import { FuncionesService } from '../../services/funtions.service';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { RouterModule,ActivatedRoute, Router } from '@angular/router';
import {SelectItem} from 'primeng/api';
import * as moment from "moment"
@Component({
  selector: 'app-config-basic',
  templateUrl: './config-basic.component.html',
  styleUrls: ['./config-basic.component.css']
})
export class ConfigBasicComponent implements OnInit {
	Inpdisplay:boolean
	formConfig:FormGroup;
	id
	grupos: SelectItem[];
	selectedEstado: string;
	estados: any[];
	checked: boolean;
	constructor(private aroute:ActivatedRoute,private fb: FormBuilder,private _router: Router,private _peticiones :PeticionesService, public _funService:FuncionesService) { }

	ngOnInit() {
		this.createForm();
		this.getAllEstados()
		this.getAllGrupos()
		this.aroute.params.subscribe( params => {
      		console.log('params["id"]',params);
          	this.id = params["id"]
      	});
      	this.getConfigBasic(this.id)
		this.Inpdisplay = true
	}


	createForm(a={
		distancia        :'',
		tolerancia       :'',
		tiempo_espera_gps:'',
		id_estado        :'',
		puede_vender     :''
	  }){
	  	this.formConfig=this.fb.group(a)
	  	this._funService.actionsOnRoute(this.formConfig.controls);
	}


	onSubmit(){
		let cNameAction = "CreateConfigBasic";
	    let value = JSON.parse(JSON.stringify(this.formConfig.value));
	    if (this.id!= null){
	      cNameAction ="UpdateConfigBasic";
	      // value.id = this.id;
	    }
	    console.log('cNameAction',cNameAction);
	    // let sendData = { username:this.formConfig.controls.username.value , password:this.formConfig.controls.password.value , selectedRoles:sRoles}
	   	this._funService._peticiones[cNameAction](value).subscribe(
	      res => {
	      	console.log("res",res)
	      	this._funService.Toast("success","info",res.msg)
	      	this.OnHIde();
	      },
	      error => {
	      	this._funService.Toast("error","Error",error)
	      	console.log("err",error)
	    })

  	}

  	getConfigBasic(id_cofig){
		this._peticiones.GetConfigBasic(id_cofig).subscribe( (response)=>{
			console.log('GetConfigBasic ------->',response);
			this.id = response["id"]
			this.checked = response["puede_vender"]==1?true:false;
			this.createForm(response)
		},(err)=>{console.log('response 33',err );});
	}

  	getAllGrupos(){
		this._peticiones.GetAllGrups().subscribe( (response)=>{
	  		let grups = []
	  		response.forEach((grup)=>{
	  			let grupo = {label:grup.descripcion, value:grup.id}
	  			grups.push(grupo)
	  		})
	  		this.grupos = grups;
	  	},(err)=>{console.log('response 33',err );});
	}

  	getAllEstados(){
	  	this._peticiones.GetAllEstados().subscribe( (response)=>{
	  		let stats = []
	  		response.forEach((estado)=>{
	  			let status = {label:estado.nombre, value:estado.id, color:estado.color}
	  			stats.push(status)
	  		})
	  		this.estados = stats;
	  		// console.log('this.estados',this.estados);
	  	},(err)=>{console.log('response 33',err );});
  	}


	OnHIde(){
		 let uri="/config";
	    // this.formEstado.reset();
	    this.Inpdisplay = false;
	    console.warn('PASANDO POR AQUI 1001');
	    this._router.navigate([uri]);
	    console.warn('PASANDO POR AQUI');
	    // this.Outdisplay.emit(false);
  	}

}



