import {  Component} from '@angular/core';
import { WebsocketService }  from './services/websocket.service'
import { FuncionesService } from './services/funtions.service'
import {MenuItem} from 'primeng/api';
import {CookieService} from 'ngx-cookie';
import { Router, ActivatedRoute } from '@angular/router';
@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent{
     items: MenuItem[];
	 perfil: MenuItem[];
     strr:any='';
	constructor(private _ws:WebsocketService,private _router: Router, private _funtions:FuncionesService,private cookieService: CookieService) {
		this._ws.ConectarWS("conectando....");
    }

    ngOnInit() {

        // console.log("localStorage.getItem('token') ", localStorage.getItem('token') );
        this.strr =  localStorage.getItem('user') 

        this.perfil = [
            {    label: 'Perfil',
                routerLink:['/perfil','registrar'],
                routerLinkActiveOptions:['/perfil','registrar']
            },
            {    
                label: 'Cerrar session',
                command: (Click) => { 
                    localStorage.clear();
                    this.cookieService.removeAll();
                    this._router.navigate(['/login'])
                }
                // routerLink:['/login'],
                // routerLinkActiveOptions:['/login']
            }
         ]
        this.items = [
            {
                label: 'Mantenimiento', icon: 'fa fa-fw fa-check',
                items:
                	[
                        {	label: 'Configuracin General',
                            routerLink:['/config'],
                            routerLinkActiveOptions:['/config']
                    	},
                        // {    label: 'Lista de correo',
                        //     routerLink:['/lista_de_correo'],
                        //     routerLinkActiveOptions:['/lista_de_correo']
                        // },
                    	{	label: 'Configuracin Basica',
                            routerLink:['/config/config_basic','registrar'],
                            routerLinkActiveOptions:['/config/config_basic','registrar']
                    	},
                    	{	label: 'Mapa',
                            routerLink:['/map'],
                            routerLinkActiveOptions:['/map']
                    	}

                    ]
                       
            },
            {
                label: 'Reportes', icon: 'fa fa-fw fa-soccer-ball-o',
                items: [

                        {
                            label: 'Terminales',
                            items: [
                            	{
                            		label: 'Aprobadas',
                            		routerLink:['/terminales/estado/','1'],
                            		routerLinkActiveOptions:['/terminales/estado/','1'],
                            	},
                            	{
                            		label: 'Establecidas Inicialmente',
                            		routerLink:['/terminales/estado/','2'],
                            		routerLinkActiveOptions:['/terminales/estado/','2'],
                            	},
                            	{
                            		label: 'En Evaluacin',
                            		routerLink:['/terminales/estado/','3'],
                            		routerLinkActiveOptions:['/terminales/estado/','3'],
                            	},
                            	{
                            		label: 'No Calificadas',
                            		routerLink:['/terminales/estado/','4'],
                            		routerLinkActiveOptions:['/terminales/estado/','4'],
                            	},
                            	{
                            		label: 'Descalificadas',
                            		routerLink:['/terminales/estado/','5'],
                            		routerLinkActiveOptions:['/terminales/estado/','5'],
                            	},
                            ]
                        },

                     //    {	
                     //        	label: 'Consorcios',
	                    //         routerLink:['/consorcios'],
	                    //         routerLinkActiveOptions:['/consorcios']
                    	// }
                ]
            }
        ];
    }

  cerrarSession(){
    localStorage.clear();
    this.cookieService.removeAll();
    this._router.navigate(['/login'])
  }


	
}



