import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { first } from 'rxjs/operators';
// import {ToasterService} from 'angular5-toaster/dist';
// import {BlockUI, NgBlockUI} from 'ng-block-ui';
import {CookieService} from 'ngx-cookie';
import {FuncionesService} from '../../services/funtions.service';
import {PeticionesService} from '../../services/peticiones.service';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})

export class LoginComponent implements OnInit {
  // @BlockUI() blockUI: NgBlockUI;
	loginForm: FormGroup;
	loading = false;
    submitted = false;
    returnUrl: string;
  constructor(private _funtions: FuncionesService, private formBuilder: FormBuilder,private _router: Router, private _peticiones :PeticionesService ,private cookieService: CookieService ) {
  	if(this._funtions.isLoggednIn()){
      this._router.navigate(["/map"])
    }
  }

  get f() { return this.loginForm.controls; }
  ngOnInit() {
    // localStorage.clear()
    // this.cookieService.removeAll();
  	this.loginForm = this.formBuilder.group({
      usuario: ['', Validators.required],
      password: ['', Validators.required]
  });

  }
  onSubmit() {
    this.submitted = true;
    this._peticiones.login(this.loginForm.value).subscribe(
      response => {
        if (response && response["token"]) {
          this._funtions.Toast("success", "success", "Bienvenido!!");
          console.log(response);
          localStorage.setItem('token',response["token"]);
          localStorage.setItem('user',response["user"]["username"]);
          this._funtions.setCookieText("user",response["user"]["username"])
          this._funtions.setCookieText("token",response["token"])
          this._router.navigate(['/map']);
        }

      },
      error => {
        let resultado;
        if (error.error && error.status !== 0) {
          resultado = this._funtions.sacarText(error.error);
        } else {
          console.log('error ..',error);
          // resultado = error.error.message;
        }
        // console.log(error.error.message)
        this._funtions.Toast("error","Error",resultado);

      }
    );

  }

}



