import { Routes, RouterModule } from '@angular/router';
import { AppComponent } from './app.component';
import { Angular2TokenService } from 'angular2-token';
import { AuthGuard } from './auth.guard';
import { NoLoginGuard } from './services/no-login.guard';

import { MapComponent } from './components/map/map.component';
import { LoginComponent } from './components/login/login.component';
import { ConfigComponent } from './components/config/config.component';
import { ConfigBasicComponent } from './components/config-basic/config-basic.component';
import { PerfilComponent } from './components/perfil/perfil.component';

import { RegColorComponent } from './components/reg-color/reg-color.component';
import { RegTerminalEstadoComponent } from './components/reg-terminal-estado/reg-terminal-estado.component';
import { RegUsuarioComponent } from './components/reg-usuario/reg-usuario.component';
import { RegRolComponent } from './components/reg-rol/reg-rol.component';
import { RegPermisoComponent } from './components/reg-permiso/reg-permiso.component';
import { RegCorreoComponent } from './components/reg-correo/reg-correo.component';

import { ListConsorciosComponent } from './components/list-consorcios/list-consorcios.component';
import { ListTerminalesComponent } from './components/list-terminales/list-terminales.component';
import { ListInterseccionesComponent } from './components/list-intersecciones/list-intersecciones.component';
import { ListTerminalesEstadoComponent } from './components/list-terminales-estado/list-terminales-estado.component';
import { ListCorreoComponent } from './components/list-correo/list-correo.component';

import { BuscarTerminalComponent } from './components/buscar-terminal/buscar-terminal.component';

 
const appRoutes: Routes = [
    // { path: '', component: HomeComponent, canActivate: [AuthGuard]},
    // { path: '', component: AppComponent, pathMatch: 'full'},
    // { path: '**', redirectTo: '/login'}
    // { path: '', component: LoginComponent, pathMatch: 'full'},
    // { path: 'login', component: LoginComponent, canActivate: [NoLoginGuard] },
    { path: 'login', component: LoginComponent },
    { path: '', component: LoginComponent },
    { path: 'map', component: MapComponent , canActivate: [AuthGuard],children:[
        { path: 'terminal/:id', component: BuscarTerminalComponent },
    ]},
    
    { path: 'consorcios', component: ListConsorciosComponent },
    { path: 'consorcio_terminales/:id', component: ListTerminalesComponent,
        children: [
            { path: 'terminal_intersecciones/:id/ver', component: ListInterseccionesComponent, canActivate:[AuthGuard] }
        ]
    },

    { path: 'lista_de_correo', component: ListCorreoComponent, canActivate:[AuthGuard],
        children: [
            { path: 'correo/registrar', component: RegCorreoComponent, canActivate:[AuthGuard] },
            { path: 'correo/:id/editar', component: RegCorreoComponent, canActivate:[AuthGuard] },
            { path: 'correo/:id/mostrar', component: RegCorreoComponent, canActivate:[AuthGuard] } 
        ]
    },

    { path: 'terminales/estado/:id', component: ListTerminalesEstadoComponent },
    { path: 'perfil/:id', component: PerfilComponent },
    
    

    { path: 'config', component: ConfigComponent , canActivate: [AuthGuard],
    	children: [
			{ path: 'color/registrar', component: RegColorComponent, canActivate:[AuthGuard] },
	        { path: 'color/:id/editar', component: RegColorComponent, canActivate:[AuthGuard] },
	        { path: 'color/:id/mostrar', component: RegColorComponent, canActivate:[AuthGuard] },

	        { path: 'estado/registrar', component: RegTerminalEstadoComponent, canActivate:[AuthGuard] },
	        { path: 'estado/:id/editar', component: RegTerminalEstadoComponent, canActivate:[AuthGuard] },
	        { path: 'estado/:id/mostrar', component: RegTerminalEstadoComponent, canActivate:[AuthGuard] },

            { path: 'usuario/registrar', component: RegUsuarioComponent, canActivate:[AuthGuard] },
            { path: 'usuario/:id/editar', component: RegUsuarioComponent, canActivate:[AuthGuard] },
            { path: 'usuario/:id/mostrar', component: RegUsuarioComponent, canActivate:[AuthGuard] },

            { path: 'rol/registrar', component: RegRolComponent, canActivate:[AuthGuard] },
            { path: 'rol/:id/editar', component: RegRolComponent, canActivate:[AuthGuard] },
            { path: 'rol/:id/mostrar', component: RegRolComponent, canActivate:[AuthGuard] },

            { path: 'permiso/registrar', component: RegPermisoComponent, canActivate:[AuthGuard] },
            { path: 'permiso/:id/editar', component: RegPermisoComponent, canActivate:[AuthGuard] },
            { path: 'permiso/:id/mostrar', component: RegPermisoComponent, canActivate:[AuthGuard] },

            { path: 'config_basic/registrar', component: ConfigBasicComponent, canActivate:[AuthGuard] },
            { path: 'config_basic/:id/editar', component: ConfigBasicComponent, canActivate:[AuthGuard] },
            { path: 'config_basic/:id/mostrar', component: ConfigBasicComponent, canActivate:[AuthGuard] }
        ]
	},

    // otherwise redirect to home
    { path: '**', redirectTo: '/', canActivate: [NoLoginGuard], pathMatch: 'full' }
];
 
export const routing = RouterModule.forRoot(appRoutes);


