@Injectable()
export class FuncionesMapService {
  private DESCONECTADO = 0;
  private CONECTADO = 1;
  private MAL_UBICADO = 2;
  constructor() { 
    // console.log('jsts', jsts);
  }
  terminalEstado(es_hoy, puede_vender) {
    let estado_de_session: number = this.DESCONECTADO;
    if (es_hoy && puede_vender) {
      estado_de_session = this.CONECTADO;
    } else if (puede_vender) {
      estado_de_session = this.DESCONECTADO;
    } else if (!puede_vender) {
      estado_de_session = this.MAL_UBICADO;
    }

    // console.log('terminalEstado', es_hoy, puede_vender);
    return estado_de_session;
  }
  isIntersect(FeatureA, FeatureB): boolean {
    const intersectionA_B = FeatureA.buffered.intersection(FeatureB.buffered);
    if (intersectionA_B.shell.points.coordinates.length > 0) {
      return true;
    } else {
      return false;
    }
  }

  lineStyle(feature) {
    const geometry = feature.getGeometry();
    const styles = [
      // linestring
      new Style({
        stroke: new Stroke({
          color: 'red',
          width: 2
        })
      })
    ];

    geometry.forEachSegment(function (end, start) {
      const dx = end[0] - start[0];
      const dy = end[1] - start[1];
      const rotation = Math.atan2(dy, dx);
      // arrows
      styles.push(new Style({
        geometry: new Point(end),
        image: new Icon({
          src: 'assets/myimages/arrow.png',
          anchor: [0.75, 0.5],
          rotateWithView: true,
          rotation: -rotation
          })
        }));
      });

    return styles;
  };



  createLine(consorcio, terminal_id, _X, _Y, _X2, _Y2, radius = 10) {
    const coords = [[parseFloat(_X), parseFloat(_Y)], [parseFloat(_X2), parseFloat(_Y2)]];
    const lineString = new LineString(coords);
    lineString.transform('EPSG:4326', 'EPSG:3857');

    // create the feature
    const pFeature = new Feature({
      geometry: lineString,
      text: 'Line_' + terminal_id
    });
    pFeature.setStyle(new Style({
      stroke: new Stroke({
        color: 'red',
        width: 5
      })
    }));
    const geometry = pFeature.getGeometry()
    pFeature.setStyle(this.lineStyle(pFeature));
    return pFeature;
  }




  createCircle(terminal, _Xg, _Yg, _X, _Y, label = '404', radius = 100, level = 1,
  consorcio = '', ultima_session = '', movido = false, se_movio = 0
  ) {

    const PARSER = new jsts.io.OL3Parser();
    PARSER['inject'](Point, LineString, LinearRing, Polygon, MultiPoint, MultiLineString, MultiPolygon, GeometryCollection);
    // console.log("proj.", proj['fromLonLat']);
    const geoCercaFeature = new Feature({
      geometry: new Point(proj['fromLonLat']([_X, _Y])),
      text: label,
      consorcio: consorcio,
      ultima_session: ultima_session,
      fecha_creacion: terminal.fecha_creacion,
      radio: radius,
      se_movio: terminal.se_movio.toFixed(2),
      prioridad: terminal.prioridad,
      puede_vender: terminal.puede_vender
    });


    const iconFeacture = new Feature({
      geometry: new Point(proj['fromLonLat']([_Xg, _Yg])),
      text: 'Icon_' + label,
      consorcio: undefined,
      ultima_session: ultima_session,
      radio: radius,
      se_movio: terminal.se_movio.toFixed(2)
    });

    const geoCercaFeactureSombra = new Feature({

      geometry: new Point(proj['fromLonLat']([_Xg, _Yg])),
      text: 'Sombra_' + label,
      consorcio: undefined,
      ultima_session: ultima_session,
      radio: radius
    });
    
    const jstsGeom = PARSER.read(geoCercaFeature.getGeometry());
    const buffered = jstsGeom.buffer(radius,0,1);
    geoCercaFeature.setGeometry(PARSER.write(buffered));

    const jstsGeom2 = PARSER.read(geoCercaFeactureSombra.getGeometry());
    const buffered2 = jstsGeom2.buffer(radius,0,1);
    geoCercaFeactureSombra.setGeometry(PARSER.write(buffered2));

    // ====== DESCONECTADO
    let stroke = new Stroke({ color: terminal.consorcio_color, width: 10 });
    let fill = new Fill({ color: 'rgba(118, 118, 118, 0.59)' });
    const text = new Text({ text: label, font: '8px sans-serif' });
    if (level === this.CONECTADO) {
      // stroke = new Stroke({color: 'rgba(255, 123, 0,0.93)',width:10});
      stroke = new Stroke({ color: terminal.consorcio_color, width: 10 });
      // fill   = new Fill({color: terminal.color_estado});
      fill = new Fill({ color: 'rgba(0, 175, 0, 0.4)' });
    } else if (level === this.MAL_UBICADO) {
      // stroke = new Stroke({color: 'rgba(0, 232, 0,0.93)',lineDash: [6],width:2});
      fill = new Fill({ color: 'rgba(255, 0, 0, 1)' });
    }


    geoCercaFeature.setStyle(new Style({
      stroke: stroke,
      fill: fill,
      text: text
    }));

    geoCercaFeactureSombra.setStyle(new Style({
      // stroke: new Stroke({color: 'rgba(255, 123, 0,0.93)',width:4}),
      fill: new Fill({ color: 'rgba(118, 118, 118, 0.33)' }),
      text: text
    }));

    let icon_color = 'loteka_green.png';
    if (terminal.prioridad === 1) {
      icon_color = 'loteka_blue.png';
    }
    if (terminal.prioridad === 3) {
      icon_color = 'loteka_red.png';
    }

    iconFeacture.setStyle(new Style({
      geometry: new Point(proj['fromLonLat']([_X, _Y])),
      image: new Icon({
        src: 'assets/myimages/' + icon_color,
        anchor: [0.2, 49.6],
        anchorXUnits: 'fraction',
        anchorYUnits: 'pixels',
        crossOrigin: 'anonymous',
        rotateWithView: true,
        rotation: 0,
        size: [60, 60],
        scale: 0.45
      }),
      stroke: stroke,
      fill: fill,
      text: text
    }));
    // console.log("geoCercaFeature",geoCercaFeature)
    geoCercaFeature['buffered'] = buffered;
    iconFeacture['buffered'] = buffered;
    geoCercaFeactureSombra['buffered'] = buffered2;
    return [geoCercaFeature, iconFeacture, geoCercaFeactureSombra];
  }

  intersect(vectores) {
    const features = vectores;

    let feat1, feat2, intersects12, intersects21;
    const parts = [];
    const inters = [];
    for (let i = 0; i < features.length - 1; ++i) {
      feat1 = features[i];
      for (let j = i + 1; j < features.length; ++j) {
        feat2 = features[j];

        intersects12 = this.isIntersect(features[i], features[j]);
        if (intersects12) {
          const comb = features[i].U.text + '_' + features[j].U.text;
          const comb2 = features[j].U.text + '_' + features[i].U.text;
          let existe_comb = false;
          for (const c in inters) {
            if (inters[c] === comb || inters[c] === comb2) {
              existe_comb = true;
            }
          }
          if (!existe_comb) {
            inters.push({ combinacion: comb });
          }
          parts.push(features[i].U.text + ' intersects ' + features[j].U.text + '\n');
        }
        intersects21 = this.isIntersect(features[j], features[i]);
        if (intersects21) {
          const comb2 = features[j].U.text + '_' + features[i].U.text;
          const comb = features[i].U.text + '_' + features[j].U.text;
          let existe_comb = false;
          for (const c in inters) {
            if (inters[c] === comb || inters[c] === comb2) {
              existe_comb = true;
            }
          }
          if (!existe_comb) {
            inters.push({ combinacion: comb2 });
          }
        }
      }
    }
    this.distribucionCorrectaInters(features, inters);
  }

  distribucionCorrectaInters(features, inters) {
    for (const terminal of features) {
      const ints = [];
      for (const c of inters) {
        const term_comb = inters[c].combinacion.split('_');
        if (term_comb[0] === features[terminal].U.text || term_comb[1] === features[terminal].U.text) {
          if (term_comb[0] === features[terminal].U.text) {
            const intAB = term_comb[0] + '_' + term_comb[1];
            ints.push({ interseccion: intAB });
          }
        }
      }

      features[terminal].inters = ints;
    }
  }
moveDecimal(n) {
  const l = n.toString().length - 3;
  const v = n / Math.pow(10, l);
  return v;
}

}



