import { Component, OnInit ,Input} from '@angular/core';
import { PeticionesService } from '../../services/peticiones.service';
@Component({
  selector: 'app-list-estados',
  templateUrl: './list-estados.component.html',
  styleUrls: ['./list-estados.component.css']
})
export class ListEstadosComponent implements OnInit {
	@Input() InpEstados;
	estados
  constructor(private _peticiones :PeticionesService) { }

  ngOnInit() {
  	this.getAllEstados()
  }

  getAllEstados(){
  	this._peticiones.GetAllEstados().subscribe( (response)=>{this.estados = response;},(err)=>{console.log('response 33',err );});
  }

}



