import { Component, OnInit } from '@angular/core';
import { PeticionesService } from '../../services/peticiones.service';
@Component({
  selector: 'app-config',
  templateUrl: './config.component.html',
  styleUrls: ['./config.component.css']
})
export class ConfigComponent implements OnInit {

  constructor(private _peticiones :PeticionesService) { }

  colores    = []
  estados    = []
  consorcios = []
  ngOnInit() {
  	// this.getAllConsorcios();
   //  this.getAllColors();
   //  this.getAllEstados();
  }


  getAllColors(){
  	this._peticiones.GetAllColores().subscribe( (response)=>{this.colores = response;},(err)=>{console.log('response 33',err );});
  }

  getAllEstados(){
  	this._peticiones.GetAllEstados().subscribe( (response)=>{this.estados = response;},(err)=>{console.log('response 33',err );});
  }

  getAllConsorcios(){
    this._peticiones.GetAllConsorcios().subscribe( (response)=>{this.consorcios = response;},(err)=>{console.log('response 33',err );});
  }

}



