import { Component, OnInit , Input } from '@angular/core';
import { PeticionesService } from '../../services/peticiones.service';
import * as moment from "moment"
@Component({
  selector: 'app-list-roles',
  templateUrl: './list-roles.component.html',
  styleUrls: ['./list-roles.component.css']
})
export class ListRolesComponent implements OnInit {
	@Input() InpRoles
	textFilter
	roles
	constructor(private _peticiones :PeticionesService) { }

	ngOnInit() {
		this.getAllRoles()
		
	}

	getAllRoles(){
		this._peticiones.GetAllRoles().subscribe( (response)=>{
			let rols = []
			response.forEach((rol)=>{
				// console.log('rol',rol);
				rol.createAt =  moment(rol.createAt).format("YYYY-MM-DD")
				rols.push(rol)
			})
			this.roles  = rols
			// console.log('REPUESTA',this.roles);
		},(err)=>{console.log('response 33',err );});
	}

  	doThingFactory(){
		return (text) => this.textFilter= text;
	}

}



