import { Component, OnInit , Input} from '@angular/core';
import { PeticionesService } from '../../services/peticiones.service';
@Component({
  selector: 'app-list-colores',
  templateUrl: './list-colores.component.html',
  styleUrls: ['./list-colores.component.css']
})
export class ListColoresComponent implements OnInit {
	@Input() InpColores;
	colores
	constructor(private _peticiones :PeticionesService) { }

	ngOnInit() {
		this.getAllColors()
	}

	getAllColors(){
  		this._peticiones.GetAllColores().subscribe( (response)=>{this.colores = response;},(err)=>{console.log('response 33',err );});
  	}

}



