import { Component, OnInit } from '@angular/core';
import { PeticionesService } from '../../services/peticiones.service';
import { FuncionesService } from '../../services/funtions.service';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { RouterModule,ActivatedRoute, Router } from '@angular/router';
import * as moment from "moment"
@Component({
  selector: 'app-reg-usuario',
  templateUrl: './reg-usuario.component.html',
  styleUrls: ['./reg-usuario.component.css']
})
export class RegUsuarioComponent implements OnInit {
	Inpdisplay: boolean;
    checked: boolean = false;
    formUser:FormGroup;
    id
    roles
    selectedRoles: string[] = [];
	constructor(private aroute:ActivatedRoute,private fb: FormBuilder,private _router: Router,private _peticiones :PeticionesService, public _funService:FuncionesService) { }

	ngOnInit() {
		// this.getAllRoles()
		this.getUser()
		this.createForm();
		this.aroute.params.subscribe( params => {
      		console.log('params["id"]',params);
          	this.id = params["id"]
      	});

		this.Inpdisplay = true;
		
	}

	onSubmit(){
		let cNameAction = "CrearUsuario";
	    let value = JSON.parse(JSON.stringify(this.formUser.value));
	    if (this.id!= null){
	      cNameAction ="UpdateUsuario";
	      // value.id = this.id;
	    }
	    let sRoles = []
	    this.selectedRoles.forEach(perm=>{sRoles.push(parseInt(perm))})
	    let sendData = { username:this.formUser.controls.username.value , password:this.formUser.controls.password.value , selectedRoles:sRoles}
	    this._funService._peticiones[cNameAction](sendData,this.id).subscribe(
	      res => {
	      	// console.log("res",res)
	      	this._funService.Toast("success","info",res.msg)
	      	this.OnHIde();
	      },
	      error => {
	      	this._funService.Toast("error","Error",error)
	      	console.log("err",error)
	    }
	  );

  	}
  	getUser(){
		this._funService._peticiones.GetUser(this.id).subscribe(
	      res => {
	      	res.estado = res.estado=="A"
	      	this.createForm(res)

	      	res.roles.forEach(rol=>{
				this.selectedRoles.push(""+rol.id+"")
	      	})
	      	this.getAllRoles()
	      },
	      error => {
	      	this._funService.Toast("error","Error",error)
	      	console.log("err",error)
	      }
	  );
	}

  	createForm(a={
	  		username:'',
	  		password:'',
	  		password2:'',
	  		selectedRoles:[],
	  		estado:true
	  }){
	  	this.formUser=this.fb.group(a)
	  	this._funService.actionsOnRoute(this.formUser.controls);
	}

  	getAllRoles(){
		this._peticiones.GetAllRoles().subscribe( (response)=>{
			response.createAt = moment(response.createAt).format("YYYY-MM-DD")
			this.roles = response;
			let users = []
			response.forEach((user)=>{
				user.createAt =  moment(this.roles.createAt).format("YYYY-MM-DD")
				users.push(user)
			})
			this.roles  = users
		},(err)=>{console.log('response 33',err );});
	}

	OnHIde(){
		 let uri="/config";
	    // this.formEstado.reset();
	    this.Inpdisplay = false;
	    this._router.navigate([uri]);
	    // this.Outdisplay.emit(false);
  	}

}



