import { Injectable } from '@angular/core';
// import {CONSTANTES} from './CONSTANTES.service';
import {$WebSocket,WebSocketSendMode,WebSocketConfig} from 'angular2-websocket/angular2-websocket'
@Injectable()
export class WebsocketService {
    private urlServeSocket = "monitorgps.orkapi.net:9292";//"localhost:8443"
    // private urlServeSocket = "192.168.0.88:9292";//"localhost:8443"
    // private urlServeSocket = "10.0.0.4:9292";//"localhost:8443"
    // private urlServeSocket = "192.168.43.104:9292";
    // private port          = CONSTANTES.port
    private socket        = null
    private collection    = []
    private onEvens       = {}
    private interval      = null
    private lIsconectado  = false
    public  ActionOnOpen  = null;
  constructor() {
  	this.onEvens = {}
  	// this.ConectarWS("conectando....");
  }


  public emit(type,data){
  	this.socket.send(JSON.stringify({type: type , data }), WebSocketSendMode.Direct)
  }

  public on(type,p){
  	// console.log("on ",type,p,this.onEvens)
    this.onEvens[type] = {
      fn : p
    }
    // console.log("this.onEvens[type]",this.onEvens)
  }
  private onMessage(message: MessageEvent){
      let data = JSON.parse(message.data)
      // console.warn("WS DATA:",data);
      if(this.onEvens[data.type]){
      	if (Object.prototype.toString.call(data.data) === '[object String]'){
      		data.data = JSON.parse(data.data);
      	}
        this.onEvens[data.type].fn(data.data);
      }
      this.collection.push(data)
  }

  private reconectarWs(mjs){
    this.lIsconectado = false
    clearInterval(this.interval);

    this.interval = setInterval(()=>{
    	// console.log("this.socket interval")
      if(!this.socket || this.socket.readyState !== 1){
        // console.log("readyState es 1");
        this.ConectarWS("Reconectando...");
      }
      else{
        clearInterval(this.interval);
      }
     }
    ,1000);
  }



  ConectarWS(msj){
    console.log(msj);
    this.socket = new $WebSocket('ws://'+this.urlServeSocket,null,{ reconnectIfNotNormalClose: true });

    console.log("socket",this.socket);
    this.socket.onClose((event)=>{
    	console.log("on close....")
	      // this.reconectarWs("reconecctando...");
	    }
    )
    this.socket.onOpen((event)=>{
	      console.log("onOpen --- pareee");
	      if(this.ActionOnOpen){
	      	this.ActionOnOpen();
	      }
	      // clearInterval(this.interval)
    	}
    )
    this.socket.onError((event)=>{
     	console.log("onError");
    	}
    )

    this.socket.onMessage( (message:MessageEvent)=>{this.onMessage(message)} )
  }


}



