import { Component, OnInit } from '@angular/core';
import {SelectItem} from 'primeng/api';
import { PeticionesService } from '../../services/peticiones.service';
import { FuncionesService } from '../../services/funtions.service';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { RouterModule,ActivatedRoute, Router } from '@angular/router';
@Component({
  selector: 'app-reg-rol',
  templateUrl: './reg-rol.component.html',
  styleUrls: ['./reg-rol.component.css']
})
export class RegRolComponent implements OnInit {
	Inpdisplay: boolean;
	formRol:FormGroup;
	rol_name=""
	id
	permisos
	selectedPermisos: any[] = [];
	constructor(private aroute:ActivatedRoute,private fb: FormBuilder,private _router: Router,private _peticiones :PeticionesService, public _funService:FuncionesService) { }

	ngOnInit() {
		this.Inpdisplay = true;
		this.getAllPermisos()
		this.createForm();
		this.aroute.params.subscribe( params => {
      		console.log('params["id"]',params);
          	this.id = params["id"]
      	});
  	}


  	onSubmit(){
		let cNameAction = "CrearRol";
	    let value = JSON.parse(JSON.stringify(this.formRol.value));
	    if (this.id!= null){
	      cNameAction ="UpdateRol";
	    }
	    let sPermisos = []
	    this.selectedPermisos.forEach(perm=>{sPermisos.push(parseInt(perm))})
	    let sendData = { "nombre_rol":this.formRol.controls.rol_name.value,"permisos":sPermisos }
	    this._funService._peticiones[cNameAction](sendData,this.id).subscribe(
	      res => {
	      	console.log("res",res)
	      	this._funService.Toast("success","info",res.msg)
	      	this.OnHIde();
	      },
	      error => {
	      	this._funService.Toast("error","Error",error.error.message || "Error de conexin")
	      	console.log("err",error.error)
	    }
	  );

  	}

  	createForm(a={
	  		rol_name:'',
	  		selectedPermisos:[],
	  		estado:true
	  }){
	  	this.formRol=this.fb.group(a)
	  	this._funService.actionsOnRoute(this.formRol.controls);
	}

  	getAllPermisos(){
  		let perms = []
		this._peticiones.GetAllPermisos().subscribe( (response)=>{
			// response.forEach((perm)=>{
			// 	perms.push( {label:perm.nombre, value:{id:1, nombre: perm.nombre, descripcion: perm.descripcion}})
			// })
			this.permisos = response;
			console.log('resp',response);
		},(err)=>{console.log('response 33',err );});
	}

  	OnHIde(){
		 let uri="/config";
	    // this.formEstado.reset();
	    this.Inpdisplay = false;
	    this._router.navigate([uri]);
	    // this.Outdisplay.emit(false);
  	}

}



