import { Component, OnInit } from '@angular/core';
import { PeticionesService } from '../../services/peticiones.service';
import { FuncionesService } from '../../services/funtions.service';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { RouterModule,ActivatedRoute, Router } from '@angular/router';
@Component({
  selector: 'app-list-intersecciones',
  templateUrl: './list-intersecciones.component.html',
  styleUrls: ['./list-intersecciones.component.css']
})
export class ListInterseccionesComponent implements OnInit {
	Inpdisplay: boolean;
	inters:any[];
	id_terminal:''
	terminal
	constructor(private aroute:ActivatedRoute,private fb: FormBuilder,private _router: Router,private _peticiones :PeticionesService, public _funService:FuncionesService) { }

	ngOnInit() {
		this.Inpdisplay = true;
		this.aroute.queryParams.subscribe( params => {
      		console.log('params["cliente...."]',JSON.parse(params.terminal));
      		this.terminal = JSON.parse(params.terminal)
      		this.id_terminal = this.terminal.id_terminal
      		this.inters = this.terminal.intersecciones
      	});
	}

	onSubmit(){

	}


	OnHIde(){
		 let uri="/consorcio_terminales/"+this.terminal.id_consorcio;
	    // this.formEstado.reset();
	    this.Inpdisplay = false;
	    this._router.navigate([uri]);
	    // this.Outdisplay.emit(false);
	}

}



