import { Component, OnInit , Input } from '@angular/core';
import { PeticionesService } from '../../services/peticiones.service';
import * as moment from "moment"
@Component({
  selector: 'app-list-permisos',
  templateUrl: './list-permisos.component.html',
  styleUrls: ['./list-permisos.component.css']
})
export class ListPermisosComponent implements OnInit {
  	@Input() InpPermisos
	permisos
	textFilter:string;
	constructor(private _peticiones :PeticionesService) { }
	ngOnInit() {
		this.getAllPermisos()
		
	}

	getAllPermisos(){
		this._peticiones.GetAllPermisos().subscribe( (response)=>{
			this.permisos = response;
		},(err)=>{console.log('response 33',err );});
	}


 	doThingFactory(){
    	return (text) => this.textFilter= text;
 	}

}



