import { Component, OnInit } from '@angular/core';
import {SelectItem} from 'primeng/api';
import { PeticionesService } from '../../services/peticiones.service';
import { FuncionesService } from '../../services/funtions.service';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { RouterModule,ActivatedRoute, Router } from '@angular/router';
@Component({
  selector: 'app-reg-permiso',
  templateUrl: './reg-permiso.component.html',
  styleUrls: ['./reg-permiso.component.css']
})
export class RegPermisoComponent implements OnInit {
	Inpdisplay: boolean;
	formPermiso:FormGroup;
	nombre_permiso;
	descripcion;
	id;
	constructor(private aroute:ActivatedRoute,private fb: FormBuilder,private _router: Router,private _peticiones :PeticionesService, public _funService:FuncionesService) { }

	ngOnInit() {
		this.Inpdisplay = true;
		this.createForm();
		if(this.id != null){
			// this.categoria();
		}
	}
	onSubmit(){
		let cNameAction = "CrearPermiso";
	    let value = JSON.parse(JSON.stringify(this.formPermiso.value));
	    if (this.id!= null){
	      cNameAction ="UpdatePermiso";
	      value.id = this.id;
	    }

	    this._funService._peticiones[cNameAction]({value},this.id).subscribe(
	      res => {
	      	console.log("res",res)
	      	this._funService.Toast("success","info",res.msg)
	      	this.OnHIde();
	      },
	      error => {
	      	this._funService.Toast("error","Error",error)
	      	console.log("err",error)
	      }
	  );

  	}

  	createForm(a={
	  		nombre_permiso:'',
	  		descripcion:'',
	  		estado:true
	  }){
	  	this.formPermiso=this.fb.group(a)
	  	this._funService.actionsOnRoute(this.formPermiso.controls);
	}

	OnHIde(){
		 let uri="/config";
	    // this.formPermiso.reset();
	    this.Inpdisplay = false;
	    this._router.navigate([uri]);
	    // this.Outdisplay.emit(false);
  	}

}



