import { Injectable } from '@angular/core';
import {Observable} from 'rxjs/Observable';
import {HttpClient, HttpHeaders} from '@angular/common/http';
@Injectable()
export class PeticionesService {
  public url: string;
  constructor(private  http: HttpClient) { 
    // this.url= "http://monitorgps.orkapi.net:9292"; //"https://localhost:8443"
    this.url= "http://192.168.0.88:9292"; //"https://localhost:8443"
    // this.url= "http://10.0.0.4:9292"; //"https://localhost:8443"
    // this.url= "http://192.168.43.104:9292"; //"https://localhost:8443"
  }

  login(data: Object) {
    const headers = new HttpHeaders().set('Content-Type', 'application/json');
    return this.http.post(this.url + '/login',JSON.stringify(data),{headers: headers})
  }

  GetAllAcciones(): Observable<any> {
    return this.http.get(this.url + '/acciones')
  }

  GetAllColores(): Observable<any> {
    return this.http.get(this.url + '/colors')
  }
  GetAllEstados(): Observable<any> {
    return this.http.get(this.url + '/estados')
  }

  GetAllConsorcios(contarTerminales=true): Observable<any> {
    if(contarTerminales){
      return this.http.get(this.url + '/consorcios')
    }else{
      return this.http.get(this.url + '/consors')
    }
  }

  GetAllPermisos(): Observable<any> {
    return this.http.get(this.url + '/permisos')
  }

  GetAllCorreosSubscritos(): Observable<any> {
    return this.http.get(this.url + '/correos')
  }

  GetAllGrups(): Observable<any> {
    return this.http.get(this.url + '/grupos')
  }

  GetAllUser(): Observable<any> {
    return this.http.get(this.url + '/users')
  }

  GetUser(id_usuario): Observable<any> {
    return this.http.get(this.url + '/user')
  }

  GetAllRoles(): Observable<any> {
    return this.http.get(this.url + '/rols')
  }

  GetAllTerminales(id): Observable<any> {
    return this.http.get(this.url + '/terminales?id_terminal='+id)
  }
  GeTerminal(id): Observable<any> {
    return this.http.get(this.url + '/terminal/'+id)
  }

  GetConsocio(id): Observable<any> {
    return this.http.get(this.url + '/consorcio/'+id)
  }
  GetConsocioTerminales(id): Observable<any> {
    return this.http.get(this.url + '/terminales/consorcio/'+id)
  }


  GetTerminalesEstados(id): Observable<any> {
    return this.http.get(this.url + '/terminales/estado/'+id)
  }

  GetAllTerminalesSession(): Observable<any> {
    return this.http.get(this.url + '/terminales/sessions')
  }


  GetConfigBasic(id):Observable<any>{
    return this.http.get(this.url + '/config_basic')
  }

  PostCreateTerminal(object): Observable<any> {
    return this.http.post(this.url + '/terminal/session',object)
  }

  CrearPermiso(object):Observable<any>{
    return this.http.post(this.url + '/permiso/create_permiso',object)
  }

  CrearRol(object):Observable<any>{
    return this.http.post(this.url + '/rol/create_rol',object)
  }

  CrearEstado(object):Observable<any>{
    return this.http.post(this.url + '/estado/create',object)
  }
  CrearUsuario(newUser):Observable<any>{
    return this.http.post(this.url + '/user/create_user',newUser)
  }

  CreateConfigBasic(object):Observable<any>{
    return this.http.post(this.url + '/config_basic/create',object)
  }

  UpdatPermiso(object):Observable<any>{
    return this.http.post(this.url + '/permiso/update',object)
  }

  // UpdatTerminal(object):Observable<any>{
  //   return this.http.post(this.url + '/terminal/update',object)
  // }

  UpdateEstado(object):Observable<any>{
    return this.http.post(this.url + '/estado/update',object)
  }

  UpdateColor(object):Observable<any>{
    return this.http.post(this.url + '/color/update',object)
  }


  UpdateConfigBasic(object):Observable<any>{
    return this.http.post(this.url + '/config_basic/update',object)
  }

  CrearColor(object):Observable<any>{
    return this.http.post(this.url + '/color/create',object)
  }

  AsignarRoles(object):Observable<any>{
    return this.http.post(this.url + '/user/reg_rol_user',object)
  }

  RegTerminalHija(object):Observable<any>{
    return this.http.post(this.url + '/terminal/reg_permitidas',object)
  }
}



