import { Component, OnInit , Input } from '@angular/core';
import { PeticionesService } from '../../services/peticiones.service';
@Component({
  selector: 'app-list-consorcios',
  templateUrl: './list-consorcios.component.html',
  styleUrls: ['./list-consorcios.component.css']
})
export class ListConsorciosComponent implements OnInit {
	@Input() InpConsorcios
	consorcios
	total_cant_terminales             =0
	total_cant_terminales_mal         =0
	porcent_total_cant_terminales     =0
	porcent_total_cant_terminales_mal =0
	textFilter:string;
	constructor(private _peticiones :PeticionesService) { }

	ngOnInit() {
		this.getAllConsorcios()
		
	}

	getAllConsorcios(){
		this._peticiones.GetAllConsorcios().subscribe( (response)=>{
			this.consorcios = response;
			this.total_cant_terminales             =0
			this.total_cant_terminales_mal         =0
			this.porcent_total_cant_terminales     =0
			this.porcent_total_cant_terminales_mal =0
			this.consorcios.forEach((consorcio,index)=>{
				this.total_cant_terminales += consorcio.cant_terminales
				this.total_cant_terminales_mal += consorcio.cant_no_pueden_vender
			})
		},(err)=>{console.log('response 33',err );});
	}

	doThingFactory(){
    	return (text) => this.textFilter= text;
 	}

}



