import { Component, OnInit } from '@angular/core';
import { PeticionesService } from '../../services/peticiones.service';
import { FuncionesService } from '../../services/funtions.service';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { RouterModule,ActivatedRoute, Router } from '@angular/router';
import {SelectItem} from 'primeng/api';
@Component({
  selector: 'app-perfil',
  templateUrl: './perfil.component.html',
  styleUrls: ['./perfil.component.css']
})
export class PerfilComponent implements OnInit {

	formConfig:FormGroup;
  	Inpdisplay:boolean
  	id
	constructor(private aroute:ActivatedRoute,private fb: FormBuilder,private _router: Router,private _peticiones :PeticionesService, public _funService:FuncionesService) { }
	
	ngOnInit() {
		this.Inpdisplay = true
	}


	createForm(a={
		distancia        :'',
		tolerancia       :'',
		tiempo_espera_gps:'',
		id_estado        :'',
		puede_vender     :''
	  }){
	  	this.formConfig=this.fb.group(a)
	  	this._funService.actionsOnRoute(this.formConfig.controls);
	}

  	onSubmit(){
		let cNameAction = "CreateConfigBasic";
	    let value = JSON.parse(JSON.stringify(this.formConfig.value));
	    if (this.id!= null){
	      cNameAction ="UpdateConfigBasic";
	      // value.id = this.id;
	    }
	    console.log('cNameAction',cNameAction);
	    // let sendData = { username:this.formConfig.controls.username.value , password:this.formConfig.controls.password.value , selectedRoles:sRoles}
	   	this._funService._peticiones[cNameAction](value).subscribe(
	      res => {
	      	console.log("res",res)
	      	this._funService.Toast("success","info",res.msg)
	      	this.OnHIde();
	      },
	      error => {
	      	this._funService.Toast("error","Error",error)
	      	console.log("err",error)
	    })

  	}

  	OnHIde(){
		 let uri="/config";
	    // this.formEstado.reset();
	    this.Inpdisplay = false;
	    this._router.navigate([uri]);
	    // this.Outdisplay.emit(false);
  	}

}



