import { Component, OnInit , Input} from '@angular/core';
import { RouterModule,ActivatedRoute, Router } from '@angular/router';
import { PeticionesService } from '../../services/peticiones.service';
@Component({
  selector: 'app-list-terminales-estado',
  templateUrl: './list-terminales-estado.component.html',
  styleUrls: ['./list-terminales-estado.component.css']
})
export class ListTerminalesEstadoComponent implements OnInit {
	textFilter:string;
	terminales;
	nombre_consorcio:'';
	id
	
	total_cant_mal          = 0
	total_cant_desactivadas = 0
	total_cant_terminales   = 0
	@Input() InpTerminalesEstado;
	constructor(private aroute:ActivatedRoute,private _peticiones :PeticionesService) { }
	ngOnInit() {
		this.aroute.params.subscribe( params => {
			console.log('params',params);
	        this.id = params.id
		    console.log('this.id',this.id);
			this.getTerminalesEstados(this.id)
	      }
	    );
	}


  	getTerminalesEstados(id_estado){
		this._peticiones.GetTerminalesEstados(id_estado).subscribe( (response)=>{
			this.terminales = response;
			console.log('response',response);
		},(err)=>{console.log('response 33',err );});
	}

	doThingFactory(){
    	return (text) => this.textFilter= text;
 	}

}



