import { Component, OnInit } from '@angular/core';
import {SelectItem} from 'primeng/api';
import { PeticionesService } from '../../services/peticiones.service';
import { FuncionesService } from '../../services/funtions.service';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { RouterModule,ActivatedRoute, Router } from '@angular/router';

interface Consorcio {
  label: string;
  value: number;
}

@Component({
  selector: 'app-reg-correo',
  templateUrl: './reg-correo.component.html',
  styleUrls: ['./reg-correo.component.css']
})


export class RegCorreoComponent implements OnInit {
	Inpdisplay: boolean;
	acciones:string[];
	formCorreo;
	id;
	consorcios: any[];
	filter = false;
	selectedAccions: any[];
	constructor(private aroute:ActivatedRoute,private fb: FormBuilder,private _router: Router,private _peticiones :PeticionesService, public _funService:FuncionesService) { }

	ngOnInit() {
		this.selectedAccions=[]
		this.Inpdisplay = true;
		this.getAllAcciones()
		this.getAllConsorcios()
		this.createForm();
	}

	onChange(item){
    	let a = this.selectedAccions.indexOf(item);
			console.log('onChange',a);
			if(a<0){
				this.selectedAccions.push(item)
			}else{
				this.selectedAccions.splice(a,1);
			}
			console.log('this.selectedAccions',this.selectedAccions);
	}

	getAllAcciones(){
  		this._funService._peticiones.GetAllAcciones().subscribe( (response)=>{
  			let accions= []
			// console.log('getAllAcciones',response);
			response.forEach((acc,index)=>{
				let accion = {label:acc.nombre, value:acc.id} 
				accions.push(accion)
			})
			// console.log('accions',accions);
			this.acciones = accions
		},(err)=>{console.log('getAllAcciones 33',err );});
  	}

  	getAllConsorcios(){
  		let consorcs= []
  		this._funService._peticiones.GetAllConsorcios(false).subscribe( (response)=>{
			// console.log('GetAllConsorcios',response);
			response.forEach((consorcio,index)=>{
				let consorc = {label:consorcio.nombre, value:consorcio.id}
				consorcs.push(consorc)
			})
			this.consorcios = consorcs
		},(err)=>{console.log('getAllAcciones 33',err );});
  	}


  	checkValue(event: any){
	   console.log(event);
	}

  	createForm(a={
	  		mail:'',
	  		consorcio:'',
	  		acciones:[],
	  		estado:true
	  }){
	  	this.formCorreo=this.fb.group(a)
	  	this._funService.actionsOnRoute(this.formCorreo.controls);
	}


  	onSubmit(){
		let cNameAction = "CrearPermiso";
	    let value = JSON.parse(JSON.stringify(this.formCorreo.value));
	    if (this.id!= null){
	      cNameAction ="UpdatePermiso";
	      value.id = this.id;
	    }

	    this.formCorreo.value.acciones = this.selectedAccions
	    console.warn('formCorreo',this.formCorreo.value,);

	   //  this._funService._peticiones[cNameAction]({value},this.id).subscribe(
	   //    res => {
	   //    	console.log("res",res)
	   //    	this._funService.Toast("success","info",res.msg)
	   //    	this.OnHIde();
	   //    },
	   //    error => {
	   //    	this._funService.Toast("error","Error",error)
	   //    	console.log("err",error)
	   //    }
	  	// );

  	}

	OnHIde(){
		 let uri="/lista_de_correo";
	    // this.formEstado.reset();
	    this.Inpdisplay = false;
	    this._router.navigate([uri]);
	    // this.Outdisplay.emit(false);
	}

}



