import { Component, OnInit } from '@angular/core';
import { PeticionesService } from '../../services/peticiones.service';
import { FuncionesService } from '../../services/funtions.service';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { RouterModule,ActivatedRoute, Router } from '@angular/router';
import {SelectItem} from 'primeng/api';
import * as moment from "moment"
@Component({
  selector: 'app-buscar-terminal',
  templateUrl: './buscar-terminal.component.html',
  styleUrls: ['./buscar-terminal.component.css']
})
export class BuscarTerminalComponent implements OnInit {


  	constructor(private aroute:ActivatedRoute,private fb: FormBuilder,private _router: Router,private _peticiones :PeticionesService, public _funService:FuncionesService) {
  		this.aroute.params.subscribe( params => {
				console.log('params["unidad"]',params);
			  this.id = params["id"]
			});

  		// this.grupos = [
    //         {label:'Select City', value:null},
    //         {label:'New York', value:{id:1, name: 'New York', code: 'NY'}},
    //         {label:'Rome', value:{id:2, name: 'Rome', code: 'RM'}},
    //         {label:'London', value:{id:3, name: 'London', code: 'LDN'}},
    //         {label:'Istanbul', value:{id:4, name: 'Istanbul', code: 'IST'}},
    //         {label:'Paris', value:{id:5, name: 'Paris', code: 'PRS'}}
    //     ];
  	}
  	grupos: SelectItem[];
  	estados: any[];
  	selectedEstado: string;

  	formTerminal:FormGroup;
    id
    paraPresentar: string[] = [];
    paraResponder:any[]
  	ngOnInit() {
  		this.getAllTerminales()
  		this.createForm()
  		this.getAllEstados()
  		this.getAllGrupos()
  		this.getTerminal(this.id)
  	}

	createForm(a={
			id_terminal              :'',
			id_grupo                 :'',
			id_consorcio             :'',
			id_estado                :'',
			lat                      :'',
			lon                      :'',
			lat_real                 :'',
			lon_real                 :'',
			nombre_consorcio         :'',
			distancia                :'',
			tolerancia               :'',
			estado                   :'',
			tiempo_espera_gps        :'',
			intersecciones_permitidas:[]
	  }){


			// console.log("intersecciones ",a.intersecciones_permitidas)
	  	let intersecciones_permitidas = JSON.parse(JSON.stringify(a.intersecciones_permitidas))
			// console.log("intersecciones",intersecciones_permitidas)
	  	this.formTerminal=this.fb.group(a)
	  	this.formTerminal.controls.intersecciones_permitidas.patchValue(intersecciones_permitidas);

	  	this._funService.actionsOnRoute(this.formTerminal.controls);
	}


  onSubmit(){
  	let cNameAction = "RegTerminalHija";
	    let value = JSON.parse(JSON.stringify(this.formTerminal.value));
	    // console.log('DATASEND...:',this.paraResponder);
	    let paraResponder = JSON.parse(JSON.stringify(this.paraPresentar))
	    let inters = []
	    paraResponder.forEach((terminal,index)=>{
	    		let accion_relacion =this.containsObject(terminal, value.intersecciones_permitidas)
	    		terminal.relacion_activa = accion_relacion
	    		if(accion_relacion=='N'){
	    			// inters.push(terminal)
	    		}
	    		// console.log("Marcado",x,terminal);
	    })
	    value.intersecciones_permitidas = inters
	    console.log('DATASEND:',value);



	    this._funService._peticiones[cNameAction](value,this.id).subscribe(
	      res => {
	      	// console.log("res",res)
	      	this._funService.Toast("success","info",res.msg)
	      	this.OnHIde();
	      },
	      error => {
	      	this._funService.Toast("error","Error",error)
	      	console.log("err",error)
	    });
  }

	containsObject(obj, list) {
    var i;
    for (i = 0; i < list.length; i++) {
        if (list[i] === obj || list[i].id_terminal === obj.id_terminal) {
        		// console.log(' list[i].id_terminal', list[i].id_terminal,obj.id_terminal);
            return 'A';
        }
    }

    return 'N';
	}

	doThingFactory(){

	}

	getTerminal(id_terminal){
		this._peticiones.GeTerminal(id_terminal).subscribe( (response)=>{
			console.log('response >>///>>>>',response);
			let terminalSeleccionada = response;
			let intersecciones =[];
			terminalSeleccionada.intersecciones_permitidas.forEach((terminalIter)=>{
				for (var i = 0; i < this.paraPresentar.length; i++) {
					let terminal = this.paraPresentar[i];
					if(terminal["id_terminal"] == terminalIter.id_terminal_hija){
						intersecciones.push(terminal)
						break
					}
				}
			})
			this.paraResponder=intersecciones
			terminalSeleccionada.intersecciones_permitidas =  intersecciones;
			this.createForm(terminalSeleccionada)
		},(err)=>{console.log('response 33',err );});
	}
	getAllGrupos(){
		this._peticiones.GetAllGrups().subscribe( (response)=>{
	  		let grups = []
	  		response.forEach((grup)=>{
	  			let grupo = {label:grup.descripcion, value:grup.id}
	  			grups.push(grupo)
	  		})
	  		this.grupos = grups;
	  	},(err)=>{console.log('response 33',err );});
	}
	getAllEstados(){
	  	this._peticiones.GetAllEstados().subscribe( (response)=>{
	  		let stats = []
	  		response.forEach((estado)=>{
	  			let status = {label:estado.nombre, value:estado.id, color:estado.color}
	  			stats.push(status)
	  		})
	  		this.estados = stats;
	  		// console.log('this.estados',this.estados);
	  	},(err)=>{console.log('response 33',err );});
  	}

  getAllTerminales(){
		this._peticiones.GetAllTerminales(this.id).subscribe( (response)=>{
			this.paraPresentar = response;
			// this.paraResponder = response
			// console.log('getAllTerminales response >>>>>>',response);
		},(err)=>{console.log('response 33',err );});
	}

	OnHIde(){
		let uri="/map";
	    this._router.navigate([uri]);
	}


}



