import { Component, OnInit , Input } from '@angular/core';
import { PeticionesService } from '../../services/peticiones.service';
import * as moment from "moment"
@Component({
  selector: 'app-list-usuarios',
  templateUrl: './list-usuarios.component.html',
  styleUrls: ['./list-usuarios.component.css']
})
export class ListUsuariosComponent implements OnInit {
	@Input() InpUsuarios
	usuarios
	textFilter:string;
	constructor(private _peticiones :PeticionesService) { }
	ngOnInit() {
		// console.log('INIT LIST USUSARIONS 000');
		this.getAllUsuarios()
		// console.log('INIT LIST USUSARIONS');
		
	}

	getAllUsuarios(){
		this._peticiones.GetAllUser().subscribe( (response)=>{
			let users = []
			response.forEach((user)=>{
				user.createAt =  moment(user.createAt).format("YYYY-MM-DD")
				users.push(user)
			})
			this.usuarios  = users
		},(err)=>{console.log('response 33',err );});
	}


 	doThingFactory(){
    	return (text) => this.textFilter= text;
 	}

}



