import { Component, OnInit , Input} from '@angular/core';
import { PeticionesService } from '../../services/peticiones.service';
import { RouterModule,ActivatedRoute, Router } from '@angular/router';
@Component({
  selector: 'app-list-terminales',
  templateUrl: './list-terminales.component.html',
  styleUrls: ['./list-terminales.component.css']
})
export class ListTerminalesComponent implements OnInit {
	textFilter:string;
	terminales;
	nombre_consorcio:'';
	id

	total_cant_mal           = 0
	total_cant_evaluacion    = 0
	total_cant_no_calificada = 0
	total_cant_descalificada = 0
	
	total_cant_terminales    = 0
	@Input() InpTerminales;
	
	constructor(private aroute:ActivatedRoute,private _peticiones :PeticionesService) { }
	
	ngOnInit() {
		this.aroute.params.subscribe( params => {
	        this.id = parseInt(params["id"])
	        this._peticiones.GetConsocio(this.id).subscribe( (response)=>{
				this.nombre_consorcio = response[0].nombre
				this.getConsorcioTerminales(this.id)
			},(err)=>{console.log('response 33',err );});
	      }
	    );
	}


  	getConsorcioTerminales(id_consorcio){
		this._peticiones.GetConsocioTerminales(id_consorcio).subscribe( (response)=>{
			this.terminales = response;
			console.log('response > getConsorcioTerminales >',response);
			response.forEach( (terminal,index)=>{
				if(!terminal.puede_vender){
					this.total_cant_mal += 1
				}
				if(terminal.id_estado==3){
					this.total_cant_evaluacion += 1
				}
				if(terminal.id_estado==4){
					this.total_cant_no_calificada += 1
				}
				if(terminal.id_estado==5){
					this.total_cant_descalificada += 1
				}
			})
			this.total_cant_terminales = response.length
		},(err)=>{console.log('response 33',err );});
	}

	doThingFactory(){
    	return (text) => this.textFilter= text;
 	}

}



